import { sendEvent } from "../..";
import { gameSettings, stateManager } from "../../core/BaseGame";
import { AdType, getStorageData, shouldPlayFirstAds, showADS } from "../../core/natives/SocialManager";
import { BaseState } from "../../core/states/BaseState";
import { TextedButton } from "../../core/ui/TextedButton";
import { GameViewState } from "./GameViewState";
import { Logo } from "./menu/Logo";
import { Scrollbox } from "pixi-scrollbox";
import { NineSlicePlane, Text, Texture } from "pixi.js";
import { ChatItem } from "./map/ChatItem";

export class MapState extends BaseState {
    private scrollbox: Scrollbox;
    public create(): void {
        const txt = new Text("TODO List", {fontFamily: "Arial", fontSize: 75, fill: 0xFFFFFF, fontWeight: "bold"});
        txt.position.set(-gameSettings.originalWidth * 0.5 + 30, -gameSettings.originalHeight * 0.5 + 40);
        this.addChild(txt);

        this.scrollbox = new Scrollbox({ boxWidth: gameSettings.originalWidth - 30, boxHeight: gameSettings.originalHeight - 160});
        this.scrollbox.position.set(-gameSettings.originalWidth * 0.5 + 18, -gameSettings.originalHeight * 0.5 + 130);
        this.addChild(this.scrollbox);

        const back = new NineSlicePlane(Texture.from("phone.png"), 104, 100, 104, 100);
        back.width = gameSettings.originalWidth + 50;
        back.height = gameSettings.originalHeight + 40;
        back.position.set(-back.width * 0.5, -back.height * 0.5);
        this.addChild(back);
    }
    public async start(...params: any) {
        sendEvent("start", "map", "level");
        const {messages, levels} = gameSettings;
        let yy = 0;
        let i = 0;
        for (const data of levels) {
            const stars = await getStorageData(data.name,"");
            if (stars !== "") {
                data.stars = parseInt(stars);
            }
            const disabled = i !== 0 && data.stars === undefined;
            const text = (i === 0) ? "Let's start from here. Tap to play." : (disabled ? "..." : messages[Math.floor(Math.random()*messages.length)]);
            const item = new ChatItem(i, data.name + (!disabled ? " " + "*".repeat(data.stars) : ""), text, i%2 !== 0);
            item.on("pointerup", () => {
                sendEvent("click", "map", "play");
                stateManager.start(GameViewState, item.id);
            });
            
            item.disabled = disabled;
            item.y = yy;
            this.scrollbox.content.addChild(item);
            yy += item.height;
            i++;
        }
    }
    public close(): void {
        sendEvent("close", "map", "level");
        this.scrollbox.content.removeChildren();
    }
}